/* Estilo para o corpo e o título */
body {
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Alterado para que o conteúdo não fique centralizado verticalmente */
  margin: 0;
  min-height: 100vh; /* Altura mínima */
  margin: 0;
  background-color: #f4f4f9;
  overflow-x: hidden; /* Evitar rolagem horizontal */
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


h1 {
  color: #28a745; /* Verde */
  font-size: 32px;
  margin-bottom: 20px;
}

/* Estilo para o input de área */
.input-area {
  padding: 10px;
  margin-right: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-top: 20px;
  width: 100%;
  max-width: 350px; /* Tamanho máximo para inputs */
}

/* Estilo para o botão */
button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #218838;
}

/* Estilo para a área que exibe as emissões */
.total-emissoes {
  margin-top: 20px;
  font-size: 18px;
  color: #333;
}

.total-emissoes p {
  margin: 5px 0;
}

.total-emissoes span {
  font-weight: bold;
}

/* Estilo para a área dos gráficos */
.grafico-container {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
  width: 100%;
  max-width: 1200px;
  padding: 0 10px;
  flex-wrap: wrap;
  overflow-x: auto; /* Habilitar rolagem horizontal apenas se necessário, como em celulares */
}

/* Estilo para cada gráfico */
.grafico {
  flex: 1 1 calc(50% - 20px);
  height: 400px;
  min-width: 300px;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Títulos dos gráficos */
.grafico h3 {
  font-size: 18px;
  color: #28a745; /* Verde */
  text-align: center;
  margin-bottom: 10px;
}

/* Texto explicativo das barras */
.barra-info {
  margin-top: 20px;
  font-size: 16px;
  text-align: center;
  color: #333;
}

/* Adicionando media query para responsividade */
@media (max-width: 768px) {
  .grafico-container {
    flex-direction: column;
    align-items: center;
    padding: 0 5px;
  }

  .grafico {
    width: 100%;
    margin-bottom: 20px; /* Espaço entre os gráficos */
  }

  /* Ajustando o tamanho do título */
  h1 {
    font-size: 28px;
  }

  /* Ajustando o tamanho do input */
  .input-area {
    max-width: 90%;
  }

  /* Ajustando o tamanho do botão */
  button {
    width: 100%;
  }
}

@media (max-width: 480px) {
  /* Ajuste de font-size para telas menores */
  h1 {
    font-size: 24px;
  }

  .total-emissoes {
    font-size: 16px;
  }

  .grafico h3 {
    font-size: 16px;
  }

  .grafico {
    height: 350px; /* Diminuir altura em telas menores */
  }
}
